.userProfile{padding:2rem;max-width:500px;margin:0 auto}.title{font-size:40px;font-weight:700;margin-bottom:.5rem}.desc{font-size:24px;font-weight:700;margin-top:40px}.desc.light{color:var(--on-surface)}.desc.dark{color:var(--surface)}.form{display:flex;flex-direction:column;gap:1rem}.form label{display:flex;flex-direction:column;font-weight:bold}.form label.light{color:var(--secondary)}.form label.dark{color:var(--secondary2)}.form label input{margin-top:.3rem;padding:.5rem;border:1px solid #ddd;border-radius:5px}.form label input.light{background-color:var(--white);color:var(--on-surface)}.form label input.dark{background-color:var(--dark2);color:var(--surface)}.levelContainer{display:flex;justify-content:space-between;align-items:stretch;margin-top:2rem;gap:2rem}.left,.right{flex:1;min-width:250px}.subtitle{font-size:1.4rem;margin-bottom:1rem;color:var(--primary)}.levelBox{border-radius:10px;padding:1rem;margin-bottom:1rem}.levelBox p{display:flex;justify-content:space-between;align-items:center;margin-bottom:.75rem}.levelBox hr{margin:1rem 0}.levelBox span{color:var(--secondary)}.updateBtn{margin-top:1rem}